body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.post{
    
    margin:20px;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    color:rgba(255, 255, 255, 0.534);
}
.post_content{
    background-color: rgba(0, 0, 0, 0.205);
    border-radius: 10px;
    padding-bottom: 10px;
    width: 500px;
    padding:10px;
}
.post_header{
    font-size:  10px;
    margin:5px;
    line-height:0px;
    width:100%; 
    display: flex;
    margin-top: 10;
}
.header_info{

    margin-top:-5px;
    margin-left:15px;
}
.post_message{
    overflow-wrap: break-word;
    margin:3px;
    
}
.post_reaction{
    
    margin-top: 5px;
    padding:0px;
    display: flex;
    justify-content: right;
    
   
}
.reaction_icon{
    margin-right:5px;
}
.reaction_icon:hover{
cursor: pointer;
background-color: rgb(97, 97, 97);
border-radius: 10px;
}
.writepost{
    
    flex: 0.6 1;
    margin:0 20px;
    display: flex;  
    justify-content: space-evenly;
    flex-direction: row;
    
}
.inputContent{
    background-color: rgba(0, 0, 0, 0.205);
    border-radius: 10px;
    padding-bottom: 10px;
    padding:10px;
    width: 500px;
}

.input{
    display: flex;
    flex-direction: row;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 10px;
    padding: 10px
}
.input >form{
    display: flex;
    width:100%; 
}
::-webkit-input-placeholder { 
    font-size:10px;
    color: rgb(255, 255, 255);
    opacity: .5; 
  }
:-ms-input-placeholder { 
    font-size:10px;
    color: rgb(255, 255, 255);
    opacity: .5; 
  }
::placeholder { 
    font-size:10px;
    color: rgb(255, 255, 255);
    opacity: .5; 
  }
#inputform{
    color:white;
    flex:1 1;
    background-color: rgba(100, 100, 100, 0);
    border:none;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
}
#postbutton{
    color:rgba(10, 10, 10, 0.212)
}

.bottom_bar{
    display: flex;
    margin-top: 5px;
    padding:0px;
    justify-content: space-between;
    margin-left:50px;
    margin-right: 50px;
  
}
.inputicon{
    color:white;
 
}
.inputicon:hover{
    background-color: rgb(97, 97, 97);
    border-radius: 10px;
    cursor: pointer;
}
